import { createGlobalStyle } from "styled-components";
import breakpoints from "../layout/breakpoints";

const GlobalStyle = createGlobalStyle`
    :root {
        --font-family: Montserrat, Calibri, sans-serif;

        --font-size-scale-ratio: 1.2;
        --font-size-xs: calc((1rem / var(--font-size-scale-ratio)) / var(--font-size-scale-ratio));
        --font-size-s: calc(var(--font-size-xs) * var(--font-size-scale-ratio));
        --font-size-m: calc(var(--font-size-s) * var(--font-size-scale-ratio));
        --font-size-l: calc(var(--font-size-m) * var(--font-size-scale-ratio));
        --font-size-xl: calc(var(--font-size-l) * var(--font-size-scale-ratio));
        --font-size-xxl: calc(var(--font-size-xl) * var(--font-size-scale-ratio));
        --font-size-xxxl: calc(var(--font-size-xxl) * var(--font-size-scale-ratio));

        --font-weight-regular: 400;
        --font-weight-semibold: 600;
        --font-weight-bold: 700;
        --font-weight-black: 900;

        --spacing-ratio: 2;
        --spacing-xxxs: calc(var(--spacing-xxs) / var(--spacing-ratio));
        --spacing-xxs: calc(var(--spacing-xs) / var(--spacing-ratio));
        --spacing-xs: calc(var(--spacing-s) / var(--spacing-ratio));
        --spacing-s: calc(var(--spacing-base) / var(--spacing-ratio));
        --spacing-base: 1rem;
        --spacing-m: calc(var(--spacing-base) * var(--spacing-ratio));
        --spacing-l: calc(var(--spacing-m) * var(--spacing-ratio));
        --spacing-xl: calc(var(--spacing-l) * var(--spacing-ratio));
        --spacing-xxl: calc(var(--spacing-xl) * var(--spacing-ratio));

        --border-base: var(--spacing-xxs) solid var(--color-neutral-6);
        --border-radius-rounded: 4px;

        --transition-base: 0.3s ease;
        --transition-fast: 0.15s ease-in-out;
        
        --color-primary-1: #f3c130;
        --color-primary-2: #91731d;
        --color-secondary-1: #000;
        --color-neutral-0: #FFF;
        --color-neutral-1: #f5f5f5;
        --color-neutral-2: #F0F0F0;
        --color-neutral-3: #d9d9d9;
        --color-neutral-4: #c4c4c4;
        --color-neutral-5: #9d9d9d;
        --color-neutral-6: #7b7b7b;
        --color-neutral-7: #3d3d3d;
        --color-neutral-8: #2B2B2B;
        --color-neutral-9: #000;

        --color-background: #F0F0F0;
        --color-text: #000;

        --img-base-size: 6rem;
        --img-scale-ratio: 2;
        --img-xs: calc(var(--img-base-size) / var(--img-scale-ratio));
        --img-s: calc(var(--img-xs) * var(--img-scale-ratio));
        --img-m: calc(var(--img-s) * var(--img-scale-ratio) * var(--img-scale-ratio));
        --img-l: calc(var(--img-m) * var(--img-scale-ratio));
        --img-xl: calc(var(--img-l) * var(--img-scale-ratio));
        --img-xxl: calc(var(--img-xl) * var(--img-scale-ratio));
    }

    .dark-mode {
        --color-primary-1: #574512;
        --color-primary-2: #f8db83;
        --color-secondary-1: #ffffffde;
        --color-neutral-0: #121212;
        --color-neutral-1: #1e1e1e;
        --color-neutral-2: #222222;
        --color-neutral-3: #262626;
        --color-neutral-4: #2B2B2B;
        --color-neutral-5: #303030;
        --color-neutral-6: #363636;
        --color-neutral-7: #3D3D3D;
        --color-neutral-8: #4F4F4F;
        --color-neutral-9: #8C8C8C;

        --color-background: #262626;
        --color-text: #ffffffde;
    }

    html {
        height: 100%;
        font-family: var(--font-family);
        font-size: 100%;
        box-sizing: border-box;

        ${breakpoints.md`
            & {
              font-size: 112.5%;
            }
        `}
    }

    body {
        position: relative;
        margin: 0;
        padding: 0;
        height: 100%;
        background-color: var(--color-neutral-0);
        color: var(--color-text);
    }

    #___gatsby {
        height: 100%;
    }

    #gatsby-focus-wrapper {
        height: 100%;
    }

    h3 + p {
        margin-top: var(--spacing-xs);
    }

    .svg-inline--fa {
        max-height: 1rem;
    }

    .gatsby-highlight pre[class*="language-"].line-numbers {
        padding-left: 2.8rem;
      }
      
    .gatsby-highlight {
        box-sizing: border-box;
        width: 100%;
        margin: 0.5rem 0;
        padding: 0.5rem;
        overflow-y: auto;
        max-width: 40rem;
        background-color: #2d2d2d;
        border-radius: 0.3em;
        
    }
    
    .gatsby-highlight pre[class*="language-"].line-numbers {
        margin: 0;
        padding: 0;
        padding-left: 2.5rem;
        overflow: initial;
    }

    :not(pre)>code[class*=language-] {
        padding: .1em 0.2em;
        border-radius: .3em;
        white-space: normal;
        word-break: break-all;
    }

    @media screen and (max-width: 768px) {
        :root {
            --img-base-size: 4rem;
        }
    }

    @media screen and (max-width: 480px) {
        :root {
            --img-base-size: 3.5rem;
        }
    }
`

export default GlobalStyle;