import React from "react";
import styled from "styled-components";
import { bool, func } from 'prop-types';
import BaseLink from "./links/base-link";
import MenuLink from "./links/menu-link";
import ThemeToggle from "./themetoggle";
import breakpoints from "../layout/breakpoints";

const Logo = styled(BaseLink)`
    display: block;
    position: relative;
    margin-left: 0;
    margin-bottom: 3.5rem;
    font-weight: var(--font-weight-bold);
    font-size: 3rem;
    text-decoration: inherit;
    color: var(--color-secondary-1);
    z-index: 3;

    .font-loading & {
      font-family: Calibri;
      font-weight: 700;
      font-size: 4.9rem;
      line-height: 1;
    }

    ${breakpoints.md`
      & {
        font-size: 4rem;
        margin-left: 2.75rem;
        margin-bottom: -1.25rem;
      }
    `}
  
`;

const Nav = styled.nav`
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 3rem;
  margin: 0;
  padding: 0 var(--spacing-m);
  background-color: var(--color-neutral-0);
  z-index: 100;
  user-select: none;
  overflow: hidden;

  ${breakpoints.md`
  & {
    position: relative;
    left: unset;
    right: unset;
    bottom: unset;
    width: auto;
    height: 4rem;
    overflow: unset;
    }
  `}
`;

const NavLinks = styled.ul`
  position: fixed;
  top: 0;
  left: 0;
  display: ${({$isOpen}) => $isOpen ? 'flex' : 'none'};
  flex-direction: column; 
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  list-style-type: none;
  z-index: 50;
  background-color: ${({$isOpen}) => $isOpen ? 'var(--color-neutral-0)' : 'none'};
  user-select: auto;

  ${breakpoints.sm`
    & {
      position: relative;
      display: flex;
      flex-direction: row; 
      align-items: center;
      width: auto;
      height: auto;
      margin: var(--spacing-base) var(--spacing-m);
    }
  `}
`;

const NavLinkItem = styled.li`
  display: flex;
  align-items: center;
  padding: var(--spacing-s) 0;
  
  ${breakpoints.sm`
    padding: 0;
  `}
`;

const MenuIcon = styled.button`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: ${({ $isOpen }) => $isOpen ? 'center' : 'space-around'};
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 75;
  transition: all var(--transition-fast);
  user-select: auto;

  &:focus, &:active {
    outline: none;
  }

  & div {
    position: relative;
    width: 100%;
    background: var(--color-neutral-9);
    transition: all var(--transition-fast);
  }

  & div:first-child, & div:last-child {
        height: ${({ $isOpen }) => $isOpen ? '0.125rem' : '0.25rem'};
    }
  
  & div:nth-child(2) {
      height: ${({ $isOpen }) => $isOpen ? '0' : '0.25rem'};
    }

  ${breakpoints.sm`
    & {
      display: none;
    }
  `}
`;


const MenuButton = ({isOpen, setIsOpen}) => {
  return (
    <MenuIcon 
      $isOpen={isOpen} 
      onClick={() => setIsOpen(!isOpen)}
      type="button"
      aria-label="Toggle menu"
      aria-haspopup="menu"
    >
      <div />
      <div />
      <div />
    </MenuIcon>
  )
}

MenuButton.propTypes = {
  isOpen: bool.isRequired,
  setIsOpen: func.isRequired,
};


const getActiveLink = (page, active) => page === active ? 'true' : "false";


const NavBar = ({active}) => {
    const [isOpen, setIsOpen] = React.useState(false);

    return (
        <>
          <Nav>
              <Logo to="/">j</Logo>
              <NavLinks $isOpen={isOpen}>
              <NavLinkItem><MenuLink to="/" activelink={getActiveLink("index", active)}>Home</MenuLink></NavLinkItem>
                <NavLinkItem><MenuLink to="/work" activelink={getActiveLink("work", active)}>Work</MenuLink></NavLinkItem>
                <NavLinkItem><MenuLink to="/blog" activelink={getActiveLink("blog", active)}> blog</MenuLink></NavLinkItem>
                <NavLinkItem><MenuLink to="/about" activelink={getActiveLink("about", active)}>About me</MenuLink></NavLinkItem>
                <NavLinkItem><MenuLink to="/contact" activelink={getActiveLink("contact", active)}>Contact</MenuLink></NavLinkItem>
                <NavLinkItem><ThemeToggle /></NavLinkItem>
              </NavLinks>
              <MenuButton isOpen={isOpen} setIsOpen={setIsOpen}/>
          </Nav>
        </>
    )
}

export { NavBar, Logo };