import React from 'react';
// Components
import GlobalStyle from '../base/globalstyle';
import { NavBar } from '../base/navbar';
import Footer from '../base/footer';
import { LayoutPageContainer, LayoutWrapper } from './layout';


const Page = (props) => {
    return (
        <>
                <GlobalStyle />
                <LayoutWrapper>
                    <NavBar active={props.active} />
                    <LayoutPageContainer>
                        {props.children}
                    </LayoutPageContainer>
                    <Footer />
                </LayoutWrapper>
        </>
    )
}

export default Page;