import React from "react";
import styled, { css } from "styled-components";
import breakpoints from './breakpoints';

const LayoutWrapper = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow-y: scroll;
`;

const LayoutPageContainer = styled.div` 
    flex: 1;

    ${breakpoints.md`
    & {
        padding: var(--spacing-m);
        padding-top: 0;
      }
    `}
`;

const LayoutContainer = styled.div`
    position: relative;
    flex: 1;
    margin-bottom: var(--spacing-m);
    background-color: var(--color-neutral-1);

    &:only-child {
        height: 100%; 
    }

    &:first-child {
        margin-top: 0;
    }
`;

const LayoutRow = ({ reverse, center, children }) => {
    return (
        <StyledLayoutRow
            $reverse={reverse}
            $center={center}
        >
            {children}
        </StyledLayoutRow>
    )
}

const StyledLayoutRow = styled.div`
    position: relative;
    display: flex;
    flex-flow: ${({ $reverse }) => $reverse ? 'column-reverse wrap' : 'column wrap'};
    flex: 1;

    ${({ $center }) => $center && css`
        justify-content: center;
        align-items: center;
    `}

    &:only-child {
        height: 100%; 
    }

    ${breakpoints.md`
        & {
            flex-flow: row wrap;
        }
    `}
`;

const LayoutItemWrapper = ({ noPadding, noTopPadding, overflowHidden, center, columnCenter, rowCenter, primaryColor, rowGap, height, children }) => {
    return (
        <StyledLayoutItemWrapper
            $noPadding={noPadding}
            $noTopPadding = {noTopPadding}
            $overflowHidden={overflowHidden}
            $center={center}
            $columnCenter={columnCenter}
            $rowCenter={rowCenter}
            $primaryColor={primaryColor}
            $rowGap={rowGap}
            $height={height}
        >
            {children}
        </StyledLayoutItemWrapper>
    )
}

const StyledLayoutItemWrapper = styled.div`
    position: relative;
    display: flex;
    flex: 1;
    flex-flow: column nowrap; 
    padding: ${({ $noPadding }) => $noPadding ? 0 : 'var(--spacing-base)'};
    ${({ $noTopPadding }) => $noTopPadding ? 'padding-top: 0' : null};
    width: 100%;
    height: auto;
    box-sizing: border-box;
    

    ${({ $overflowHidden }) => $overflowHidden && css`
        overflow: hidden;
    `}

    ${({ $center }) => $center && css`
        justify-content: center;
        align-items: center;
    `}

    ${({ $columnCenter }) => $columnCenter && css`
        justify-content: center;
    `}

    ${({ $rowCenter }) => $rowCenter && css`
        align-items: center;
    `}

    ${({ $primaryColor }) => $primaryColor && css`
        background-color: var(--color-primary-1);
    `}

    ${({ $rowGap }) => $rowGap && css`
        row-gap: ${({ $rowGap }) => typeof $rowGap !== "boolean" ? $rowGap : 'var(--spacing-base)'};
    `}

    &:first-child {
        margin-top: 0;
    }

    ${breakpoints.md`
        & {
            width: auto;
            height: ${({ $height }) => $height ? `clamp(var(--spacing-m), ${$height}, calc(var(--spacing-xxl) * 2.5))` : '100%'};
            padding: ${({ $noPadding }) => $noPadding ? 0 : 'var(--spacing-l) var(--spacing-m)'};
            ${({ $noTopPadding }) => $noTopPadding ? 'padding-top: 0' : null};
        }
    `}
`;

const LayoutItem = ({ center, maxWidth, fullWidth, children }) => {
    return (
        <StyledLayoutItem
            $center={center}
            $maxWidth={maxWidth}
            $fullWidth={fullWidth}
        >
            {children}
        </StyledLayoutItem>
    )
};


const StyledLayoutItem = styled.section`
    position: relative;
    display: inline-flex;
    flex-flow: column wrap;
    align-items: flex-start;
    word-break: break-word;

    ${({ $center }) => $center && css`
        align-items: center;
    `}

    ${({ $maxWidth }) => $maxWidth && css`
        display: inline-flex;
        max-width: ${({ $maxWidth }) => $maxWidth === true ? '25rem' : $maxWidth};
    `}

    ${({ $fullWidth }) => $fullWidth && css`
        width: 100%;
    `}

`;

const LayoutGrid = ({ columnCenter, maxWidth, children }) => {
    return (
        <StyledLayoutGrid
            $columnCenter={columnCenter}
            $maxWidth={maxWidth}
        >
            {children}
        </StyledLayoutGrid>
    )
}

const StyledLayoutGrid = styled.ol`
    display: flex;
    flex-flow: row wrap;
    gap: var(--spacing-m);
    width: 100%;
    margin: var(--spacing-m) 0;
    padding: 0;
    list-style: none;

    ${({ $columnCenter }) => $columnCenter && css`
        justify-content: center;
    `}

    ${({ $maxWidth }) => $maxWidth && css`
        max-width: ${$maxWidth};
    `}
`;


export {
    LayoutWrapper,
    LayoutPageContainer,
    LayoutContainer,
    LayoutRow,
    LayoutItemWrapper,
    LayoutItem,
    LayoutGrid,
};