import styled, { css } from "styled-components";
import React from "react";

const Heading = ({ level, center, marginTop, marginBottom, noMargin, children }) => {
    const tag = `h${level}`;
    return (
        <HeaderObject
            as={tag}
            $level={level}
            $center={center}
            $marginTop={marginTop}
            $marginBottom={marginBottom}
            $noMargin={noMargin}>
            {children}
        </HeaderObject>
    );
}

const HeaderObject = styled.h2`
    font-size: ${({ $level }) => {
        switch ($level) {
            case 1: return 'var(--font-size-xxxl)';
            case 2: return 'var(--font-size-xxl)';
            case 3: return 'var(--font-size-xl)';
            case 4: return 'var(--font-size-l)';
            case 5: return 'var(--font-size-m)';
            default: return 'var(--font-size-m)';
        }
    }};

    ${({ $noMargin }) => $noMargin && css`
        margin: 0;
    `}
    margin-top: ${({ $marginTop }) => $marginTop || '0'};
    margin-bottom: ${({ $marginBottom }) => $marginBottom || 'var(--spacing-s)'};
    
    ${({ $center }) => $center && css`
        text-align: center;
    `}


`;

const Text = ({ tag, level, semibold, bold, black, italic, center, noMargin, noTopMargin, children }) => {
    return (
        <TextObject
            as={tag}
            $level={level}
            $semibold={semibold}
            $bold={bold}
            $black={black}
            $italic={italic}
            $center={center}
            $noMargin={noMargin}
            $noTopMargin={noTopMargin}
        >
            {children}
        </TextObject>
    );
};


const TextObject = styled.p` 
    font-size: ${({ $level }) => {
        switch ($level) {
            // Base level
            case 3: return 'var(--font-size-m)';
            case 4: return 'var(--font-size-s)';
            case 5: return 'var(--font-size-xs)';
            default: return 'var(--font-size-m)';
        }
    }};
    ${({ $semibold }) => $semibold && css`
        font-weight: var(--font-weight-semibold);
    `}
    ${({ $bold }) => $bold && css`
        font-weight: var(--font-weight-bold);
`   }
    ${({ $black }) => $black && css`
        font-weight: var(--font-weight-black);
    `}
    ${({ $italic }) => $italic && css`
        font-style: italic;
    `}
    ${({ $center }) => $center && css`
        text-align: center;
    `}
    ${({ $noMargin }) => $noMargin && css`
        margin: 0;
    `}
    ${({ $noTopMargin }) => $noTopMargin && css`
        margin-top: 0;
    `}
`;



export { Heading, Text };