import { css } from 'styled-components';

const breakpointsMap = {
    xs: '480px',
    sm: '768px',
    md: '992px',
    lg: '1200px'
  };

export const breakpoints = Object.keys(breakpointsMap).reduce(
  (accumulator, label) => {
    accumulator[label] = (...args) => css`
      @media (min-width: ${breakpointsMap[label]}) {
        ${css(...args)};
      }
    `;
    return accumulator;
  },
  {}
);

  export default breakpoints;